#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

input + label {
  position: fixed;
  top: 40px;
  right: 40px;
  height: 20px;
  width: 15px;
  z-index: 5;
  span {
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    margin-top: -1px;
    left: 0;
    display: block;
    transition: 0.5s;
  }
  span:first-child {
    top: 3px;
  }
  span:last-child {
    top: 16px;
  }
}
label:hover {
  cursor: pointer;
}

input ~ nav {
  display: grid;
  place-items: center;
  background: transparent;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  z-index: 3;
  transition: 0.5s;
  transition-delay: 0.5s;
  overflow: hidden;
  > * {
    opacity: 0;
    transition: 0.5s;
    transition-delay: 0s;
    text-align: center;
  }
}
input:checked ~ nav {
  background-image: linear-gradient(
    235.59deg,
    #00062eda 87.06%,
    #101b65e1 28.36%
  );
  height: 100%;
  transition-delay: 0s;
  > * {
    opacity: 1;
  }
}
